import {
  Alert,
  Breadcrumb,
  Button,
  Card,
  Flex,
  Layout,
  Skeleton,
  Typography,
  List,
  Tag,
  Row,
  Col,
} from 'antd';
import SideBar from '../../components/SideBar/SideBar.tsx';
import { useQuery } from '@tanstack/react-query';
import { fetchApps } from '../../api/client.ts';
import { useNavigate } from 'react-router';
import ContentFooter from '../../components/Footer/Footer.tsx';
import { Application } from '../Applications/types.ts';
import { tagsColorMap } from '../../utils/tags.ts';

import './index.css';
import { lazy, Suspense } from 'react';

const { Title, Text } = Typography;
const { Content } = Layout;

const Instruction = lazy(() => import('./Instruction.tsx'));

const Main = () => {
  const navigate = useNavigate();

  const { isPending, data } = useQuery({
    queryKey: ['apps'],
    queryFn: fetchApps,
  });

  const breadcrumb = [
    {
      title: 'Home',
    },
  ];

  return (
    <SideBar>
      <Layout>
        <Content
          style={{
            paddingRight: 50,
            paddingTop: 20,
          }}
        >
          <Breadcrumb separator=">" items={breadcrumb} />
          <Flex align="center" justify="space-between">
            <Title level={2}>Dashboard</Title>
          </Flex>

          <Alert
            message={<Text>Welcome to Revopush üçæ</Text>}
            type="info"
            closable
          />
          <br />
          <Row gutter={24}>
            <Col span={12}>
              <Card
                title="Applications"
                className="dashboard-app"
                style={{ width: '100%' }}
                extra={
                  <Button onClick={() => navigate('/applications')} type="text">
                    All Applications
                  </Button>
                }
              >
                <Skeleton loading={isPending}>
                  {data?.apps?.length > 0 ? (
                    <List<Application>
                      itemLayout="horizontal"
                      dataSource={data?.apps}
                      size="large"
                      renderItem={(item) => (
                        <List.Item
                          onClick={() => navigate(`/applications/${item.name}`)}
                        >
                          <List.Item.Meta
                            title={
                              <Flex>
                                <Text>{item.name}</Text>
                              </Flex>
                            }
                            className="table-cursor"
                            description={
                              item?.deployments?.length > 0 ? (
                                item?.deployments?.map((deployment, index) => (
                                  <Tag color={tagsColorMap[index]} key={index}>
                                    {deployment}
                                  </Tag>
                                ))
                              ) : (
                                <Text type="secondary">No deployments yet</Text>
                              )
                            }
                          />
                        </List.Item>
                      )}
                    />
                  ) : (
                    <Flex vertical justify="center" align="center">
                      <Title level={5}>No applications</Title>
                      <Text type="secondary">
                        Create first application to get started with Revopush
                      </Text>
                      <br />
                      <Button
                        onClick={() => navigate('/applications')}
                        type="default"
                      >
                        Create an Application
                      </Button>
                      <br />
                    </Flex>
                  )}
                </Skeleton>
              </Card>
            </Col>
            <Col span={12}>
              <Card title="Support" style={{ width: '100%', height: '100%' }}>
                <Flex justify="center" align="center" vertical>
                  <Title level={5}>Having trouble using Revopush?</Title>
                  <Text type="secondary">
                    Message us to:{' '}
                    <a
                      href="mailto:support@revopush.org"
                      target="_blank"
                      rel="noreferrer"
                    >
                      support@revopush.org
                    </a>
                  </Text>
                </Flex>
              </Card>
            </Col>
          </Row>
          <br />
          <br />
          <Flex justify="space-between" flex={24}>
            <Suspense fallback={<Skeleton loading />}>
              <Instruction apiServer="https://api.revopush.org/" />
            </Suspense>
          </Flex>
        </Content>
      </Layout>
      <ContentFooter />
    </SideBar>
  );
};

export default Main;
